import React, { useRef } from "react";
import { Stars, useTexture } from "@react-three/drei";
import * as THREE from "three";
import { useFrame } from "react-three-fiber";
import dayMapRaw from "./textures/daymap.jpg";
import normalMapRaw from "./textures/normal_map.jpg";
import specularMapRaw from "./textures/specular_map.jpg";
import cloudsMapRaw from "./textures/clouds.jpg";

export default function Earth(props) {
  const [colorMap, normalMap, specularMap, cloudsMap] = useTexture([
    dayMapRaw,
    normalMapRaw,
    specularMapRaw,
    cloudsMapRaw,
  ]);
  const earthRef = useRef();
  const cloudRef = useRef();

  useFrame(({ clock }) => {
    //Se ejecuta cada frame
    if (!!earthRef.current) {
      const elapseTime = clock.getElapsedTime();
      earthRef.current.rotation.y = elapseTime / 6;
      cloudRef.current.rotation.y = elapseTime / 6;
    }
  });

  return (
    <>
      {/* <OrbitControls
        enableZoom
        enablePan
        enableRotate
        zoomSpeed={0.6}
        panSpeed={0.5}
        rotateSpeed={0.4}
        side={THREE.DoubleSide}
      /> */}

      <pointLight color="#FFFFFF" position={[-3, 0, 4]} intensity={2} />

      <Stars
        radius={100}
        depth={100}
        count={2000}
        factor={7}
        fade
        saturation={0}
      />
      {/* clouds */}
      <mesh ref={cloudRef}>
        <sphereGeometry args={[2.01, 32, 32]} />
        <meshPhongMaterial
          map={cloudsMap}
          opacity={0.4}
          depthWrite
          transparent
          side={THREE.DoubleSide}
        />
      </mesh>
      {/* earth */}
      <mesh ref={earthRef}>
        <sphereGeometry args={[2, 32, 32]} />
        <meshPhongMaterial specularMap={specularMap} />
        <meshStandardMaterial
          map={colorMap}
          normalMap={normalMap}
          metalness={0.4}
          roughness={0.7}
        />
      </mesh>
    </>
  );
}
