import {
  Environment,
  OrbitControls,
  PerspectiveCamera,
  Stats,
} from "@react-three/drei";
import * as THREE from "three";
import {
  BoxGeometry,
  Color,
  CylinderGeometry,
  SphereGeometry,
  TextureLoader,
  Vector2,
} from "three";
import { useRef } from "react";
import { mergeBufferGeometries } from "three/examples/jsm/utils/BufferGeometryUtils";
import hdrMap from "./Textures/envmap.hdr";
import { useFrame } from "@react-three/fiber";
import { SimplexNoise } from "simplex-noise";
import dirtRaw from "./Textures/dirt.png";
import dirt2Raw from "./Textures/dirt2.jpg";
import grassRaw from "./Textures/grass.jpg";
import sandRaw from "./Textures/sand.jpg";
import stoneRaw from "./Textures/stone.png";
import waterRaw from "./Textures/water.jpg";

//Const
const MAX_HEIGHT = 10;
const STONE_HEIGHT = MAX_HEIGHT * 0.8;
const DIRT_HEIGHT = MAX_HEIGHT * 0.7;
const GRASS_HEIGHT = MAX_HEIGHT * 0.5;
const SAND_HEIGHT = MAX_HEIGHT * 0.3;
const WATER_HEIGHT = MAX_HEIGHT * 0.15;
const DIRT2_HEIGHT = MAX_HEIGHT * 0;

export default function Map(props) {
  const reff = useRef();
  const simplex = new SimplexNoise();

  const textures = {
    dirt: new TextureLoader().load(dirtRaw),
    stone: new TextureLoader().load(stoneRaw),
    sand: new TextureLoader().load(sandRaw),
    grass: new TextureLoader().load(grassRaw),
    dirt2: new TextureLoader().load(dirt2Raw),
    water: new TextureLoader().load(waterRaw),
  };

  let dirtGeo = new BoxGeometry(0, 0, 0);
  let sandGeo = new BoxGeometry(0, 0, 0);
  let dirt2Geo = new BoxGeometry(0, 0, 0);
  let grassGeo = new BoxGeometry(0, 0, 0);
  let stoneGeo = new BoxGeometry(0, 0, 0);
  let cloudsGeo = new SphereGeometry(0, 0, 0);

  const Stone = (height, pos) => {
    const px = Math.random() * 0.4;
    const pz = Math.random() * 0.4;

    const geo = new SphereGeometry(Math.random() * 0.3 + 0.1, 7, 7);
    geo.translate(pos.x + px, height, pos.y + pz);
    return geo;
  };

  const Tree = (height, pos) => {
    const treeHeight = Math.random() * 1 + 1.25;

    const geo1 = new CylinderGeometry(0, 1.5, treeHeight, 3);
    geo1.translate(pos.x, height + treeHeight * 0 + 1, pos.y);

    const geo2 = new CylinderGeometry(0, 1.15, treeHeight, 3);
    geo2.translate(pos.x, height + treeHeight * 0.6 + 1, pos.y);

    const geo3 = new CylinderGeometry(0, 0.8, treeHeight, 3);
    geo3.translate(pos.x, height + treeHeight * 1.25 + 1, pos.y);
    return mergeBufferGeometries([geo1, geo2, geo3]);
  };

  const Cloud = () => {
    let count = Math.floor(Math.pow(Math.random(), 0.45) * 4);

    for (let i = 0; i < count; i++) {
      const puff1 = new SphereGeometry(1.2, 7, 7);
      const puff2 = new SphereGeometry(1.5, 7, 7);
      const puff3 = new SphereGeometry(0.9, 7, 7);

      puff1.translate(-1.85, Math.random() * 0.3, 0);
      puff2.translate(0, Math.random() * 0.3, 0);
      puff3.translate(1.85, Math.random() * 0.3, 0);

      const clouds = mergeBufferGeometries([puff1, puff2, puff3]);
      clouds.translate(
        Math.random() * 20 - 10,
        Math.random() * 7 + 10,
        Math.random() * 20 - 10
      );
      clouds.rotateY(Math.random() * Math.PI * 2);
      cloudsGeo = mergeBufferGeometries([cloudsGeo, clouds]);
    }
  };

  const makeHex = (height, pos) => {
    const geo = new CylinderGeometry(1, 1, height, 6, 1, false);
    geo.translate(pos.x, height * 0.5, pos.y);

    if (height > STONE_HEIGHT) {
      stoneGeo = mergeBufferGeometries([stoneGeo, geo]);
      if (Math.random() > 0.8) {
        stoneGeo = mergeBufferGeometries([stoneGeo, Stone(height, pos)]);
      }
    } else if (height > DIRT_HEIGHT) {
      dirtGeo = mergeBufferGeometries([dirtGeo, geo]);
    } else if (height > GRASS_HEIGHT) {
      grassGeo = mergeBufferGeometries([grassGeo, geo]);
      if (Math.random() > 0.8) {
        grassGeo = mergeBufferGeometries([grassGeo, Tree(height, pos)]);
      }
    } else if (height > SAND_HEIGHT) {
      sandGeo = mergeBufferGeometries([sandGeo, geo]);
      if (Math.random() > 0.8) {
        sandGeo = mergeBufferGeometries([sandGeo, Stone(height, pos)]);
      }
    } else if (height > DIRT2_HEIGHT) {
      dirt2Geo = mergeBufferGeometries([dirt2Geo, geo]);
    }
  };

  const tilePos = (tileX, tileY) => {
    //creo el patro de hexagonos que va de un lado al otro
    return new Vector2((tileX + (tileY % 2) * 0.5) * 1.77, tileY * 1.535);
  };

  //  Posisionamiento
  for (let xi = -15; xi <= 15; xi++) {
    for (let ji = -15; ji <= 15; ji++) {
      let position = tilePos(xi, ji);
      if (position.length() > 16) {
        continue;
      }

      let noise = (simplex.noise2D(xi * 0.1, ji * 0.1) + 1) * 0.5;
      noise = Math.pow(noise, 1.5);

      makeHex(noise * MAX_HEIGHT, tilePos(xi, ji));
    }
  }
  Cloud();

  useFrame(() => {
    //  LOOP
    if (!!reff.current) {
      reff.current.rotation.y += 0.001;
    }
  });

  return (
    <>
      <Stats />
      <PerspectiveCamera makeDefault position={[0, 20, 50]} castShadow />
      <OrbitControls dampingFactor={0.5} enableDamping />
      <pointLight
        intensity={1.2}
        color={new Color("white").convertSRGBToLinear().convertSRGBToLinear()}
        castShadow
        position={[10, 20, 20]}
      />
      <ambientLight intensity={0.2} />

      <object3D ref={reff}>
        <mesh geometry={cloudsGeo} castShadow>
          <meshStandardMaterial envMapIntensity={0.75} flatShading />
        </mesh>

        <mesh geometry={dirtGeo} castShadow receiveShadow>
          <meshPhysicalMaterial
            envMapIntensity={0.135}
            flatShading
            map={textures.dirt}
            metalness={0.7}
            roughness={0.4}
          />
        </mesh>
        <mesh geometry={stoneGeo} castShadow receiveShadow>
          <meshPhysicalMaterial
            envMapIntensity={0.135}
            flatShading
            map={textures.stone}
            metalness={0.7}
            roughness={0.4}
          />
        </mesh>
        <mesh geometry={sandGeo} castShadow receiveShadow>
          <meshPhysicalMaterial
            envMapIntensity={0.135}
            flatShading
            map={textures.sand}
            metalness={0.7}
            roughness={0.4}
          />
        </mesh>
        <mesh geometry={grassGeo} castShadow receiveShadow>
          <meshPhysicalMaterial
            envMapIntensity={0.135}
            flatShading
            map={textures.grass}
            metalness={0.7}
            roughness={0.4}
          />
        </mesh>
        <mesh geometry={dirt2Geo} castShadow receiveShadow>
          <meshPhysicalMaterial
            envMapIntensity={0.135}
            flatShading
            map={textures.dirt2}
            metalness={0.7}
            roughness={0.4}
          />
        </mesh>
        <mesh receiveShadow position={[0, 1, 0]}>
          <cylinderGeometry args={[17, 17, WATER_HEIGHT, 50]} />
          <meshPhysicalMaterial
            envMapIntensity={0.2}
            map={textures.water}
            metalness={0.025}
            roughness={1}
            ior={1.4}
            transmission={1}
            transparent
            thickness={10}
            color={new Color("#55aaff").convertSRGBToLinear().multiplyScalar(3)}
            side={THREE.DoubleSide}
          />
        </mesh>
        <mesh position={[0, 0.5, 0]}>
          <cylinderGeometry
            args={[17.03, 17.03, WATER_HEIGHT + 1, 50, 1, true]}
          />
          <meshBasicMaterial
            map={textures.dirt2}
            color="grey"
            side={THREE.DoubleSide}
          />
        </mesh>
        <mesh position={[0, -0.5, 0]}>
          <cylinderGeometry args={[18, 18, WATER_HEIGHT + 1, 50]} />
          <meshBasicMaterial map={textures.dirt2} side={THREE.DoubleSide} />
        </mesh>
      </object3D>
      <Environment files={hdrMap} />
    </>
  );
}
