import * as THREE from "three";
import { useFrame } from "react-three-fiber";
import { OrbitControls, PerspectiveCamera } from "@react-three/drei";
import { forwardRef, useRef } from "react";

export default function Reloj() {
  const ringRef1 = useRef();
  const ringRef2 = useRef();
  const ringRef3 = useRef();
  const secRef = useRef();
  const minRef = useRef();
  const hourRef = useRef();

  useFrame(({ mouse }) => {
    const date = new Date();
    const { x, y } = mouse;

    if (!!ringRef1.current) {
      const { rotation } = ringRef1.current;
      rotation.x = rotation.x * 0.99 + -x * 0.5 * 0.01;
      rotation.y = rotation.y * 0.99 + y * 0.5 * 0.01;
    }

    if (!!ringRef2.current) {
      const { rotation } = ringRef2.current;
      rotation.x = rotation.x * 0.99 + -y * 0.2 * 0.01;
      rotation.y = rotation.y * 0.99 + x * 0.2 * 0.01;
    }

    if (!!ringRef3.current) {
      const { rotation } = ringRef3.current;
      rotation.x = rotation.x * 0.99 + y * 0.3 * 0.01;
      rotation.y = rotation.y * 0.99 + -x * 0.3 * 0.01;
    }

    if (!!secRef.current) {
      const { rotation } = secRef.current;
      rotation.z = -2 * Math.PI * 1 * ((date.getSeconds() - 60) / 60);
    }
    if (!!minRef.current) {
      const { rotation } = minRef.current;
      rotation.z = -2 * Math.PI * 1 * ((date.getMinutes() - 360) / 360);
    }
    if (!!hourRef.current) {
      const { rotation } = hourRef.current;
      rotation.z = -2 * Math.PI * 1 * ((date.getHours() - 24) / 24);
    }
  });

  return (
    <>
      <PerspectiveCamera makeDefault position={[0, 0, 15]} />
      <OrbitControls
        maxAzimuthAngle={35}
        maxPolarAngle={35}
        makeDefault
        maxZoom={2}
      />
      <ambientLight position={[0, 0, 10]} intensity={0.1} />
      <pointLight position={[0, 0, 10]} intensity={0.8} />
      <Ring radius={3.5} color="#8a9cba" scale={1} ref={ringRef1} />
      <Ring radius={4.7} color="#657185" scale={0.75} ref={ringRef2} />
      <Ring radius={6} color="#2c313b" scale={0.4} ref={ringRef3} />
      <object3D ref={secRef} position={[0, 0, 0.5]}>
        <Line height={1} width={0.5} depth={0.5} color="white" />
      </object3D>
      <object3D ref={minRef}>
        <Line height={2} width={0.5} depth={0.5} color="blue" />
      </object3D>
      <object3D ref={hourRef} position={[0, 0, -0.5]}>
        <Line height={3} width={0.5} depth={0.5} color="red" />
      </object3D>
    </>
  );
}

const Ring = forwardRef(({ radius, scale, color }, ref) => {
  return (
    <object3D ref={ref}>
      <mesh position={[0, 0, 0.5 / 2]}>
        <ringGeometry args={[radius, radius + 1 * scale, 60]} />
        <meshStandardMaterial
          color={color}
          roughness={0}
          side={THREE.DoubleSide}
        />
      </mesh>
      <mesh rotation={[Math.PI / 2, 0, 0]}>
        <cylinderGeometry args={[radius, radius, 0.5, 60, 1, true]} />
        <meshStandardMaterial
          metalness={0.3}
          roughness={0}
          color="white"
          side={THREE.DoubleSide}
        />
      </mesh>
      <mesh rotation={[Math.PI / 2, 0, 0]}>
        <cylinderGeometry
          args={[radius + 1 * scale, radius + 1 * scale, 0.5, 60, 1, true]}
        />
        <meshStandardMaterial
          color="white"
          roughness={0}
          side={THREE.DoubleSide}
        />
      </mesh>
    </object3D>
  );
});

const Line = forwardRef(({ height, width, depth, color }, ref) => {
  return (
    <object3D ref={ref} position={[0, height / 2, 0.25]}>
      <mesh rotation={[0, 0, Math.PI * 0.5]}>
        <boxBufferGeometry args={[height, width, depth]} />
        <meshStandardMaterial side={THREE.DoubleSide} color={color} />
      </mesh>
      <mesh rotation={[Math.PI * 0.5, 0, 0]} position={[0, height * 0.5, 0]}>
        <cylinderBufferGeometry args={[width * 0.5, width * 0.5, depth, 10]} />
        <meshStandardMaterial side={THREE.DoubleSide} color={color} />
      </mesh>
      <mesh rotation={[Math.PI * 0.5, 0, 0]} position={[0, -height * 0.5, 0]}>
        <cylinderBufferGeometry args={[width * 0.5, width * 0.5, depth, 10]} />
        <meshStandardMaterial side={THREE.DoubleSide} color={color} />
      </mesh>
    </object3D>
  );
});
