import { createRef, Suspense, useState } from "react";
import { Canvas } from "react-three-fiber";
import BtBack from "../Header/btBack";
import Map from "./map";
import styled from "styled-components";

const ButtonSty = styled.button`
  position: absolute;
  top: 3rem;
  z-index: 10;
`;

export default function Index() {
  const [refresh, setRef] = useState(createRef());
  return (
    <>
      <BtBack />
      <ButtonSty ref={refresh} onClick={() => setRef(createRef())}>
        Refresh
      </ButtonSty>
      <Canvas className="canvas cream" shadows>
        <Suspense fallback={null}>
          <Map />
        </Suspense>
      </Canvas>
    </>
  );
}
