import { OrbitControls, PerspectiveCamera, SpotLight } from "@react-three/drei";
import { angleToRadians } from "../../Utilitys/angle";
import Candle from "./Candle";

export default function CandleScene() {
  return (
    <>
      <PerspectiveCamera makeDefault fov={75} position={[0, 3, 5]} />
      <OrbitControls maxPolarAngle={Math.PI * 0.35} />
      {/* Spotlight light */}
      <spotLight
        args={["#ffffff", 1.5, 7, angleToRadians(45), 0.4]}
        position={[0, 1, 3]}
        castShadow
      />
      <Candle />
      <mesh>
        <boxGeometry args={[5, 5, 5]} />
        <meshBasicMaterial color={0x2f95d3} />
      </mesh>
    </>
  );
}
