import { useEffect } from "react";
import { useFrame, useLoader } from "react-three-fiber";
import {
  AlwaysStencilFunc,
  DoubleSide,
  EquirectangularReflectionMapping,
  LinearEncoding,
  ReplaceStencilOp,
  Scene,
  TextureLoader,
  WebGLRenderTarget,
} from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import FillQuad from "./FielQuad";

//Dentro del portal voy a renderizar otra escena con la galaxia de fondo
//Tengo que generar toda la escena desde cero
const scene = new Scene();
scene.background = new TextureLoader().load(
  process.env.PUBLIC_URL + "/textures/galaxy.jpg",
  (texture) => {
    texture.encoding = LinearEncoding;
    //Para hacer un background de 360 grados viene esta propiedad
    texture.mapping = EquirectangularReflectionMapping;
  }
);

//Creo un render interno para poder mostrar otras ecenas
const target = new WebGLRenderTarget(window.innerWidth, window.innerHeight);

window.addEventListener("resize", () => {
  target.setSize(window.innerWidth, window.innerHeight);
});

export default function Portal() {
  const model = useLoader(
    GLTFLoader,
    process.env.PUBLIC_URL + "/models/portal.glb"
  );
  const mask = useLoader(
    GLTFLoader,
    process.env.PUBLIC_URL + "/models/portal_mask.glb"
  );

  useFrame((state) => {
    //Necesario para renderizar la escena
    state.gl.setRenderTarget(target); //Para decirle al render que lo renderice
    state.gl.render(scene, state.camera);
    //El proximo render es de la escena general y no la textura
    state.gl.setRenderTarget(null);
  });

  useEffect(() => {
    if (!model) return;
    let mesh = model.scene.children[0]; //It's the only mesh in the import
    mesh.material.envMapIntensity = 3.5;

    if (!mask) return;
    let maskMesh = mask.scene.children[0];
    maskMesh.material.side = DoubleSide;

    //Le digo al render que parte de la escena renderizar con un stencilBuffer
    //Es como tener la escena con una matriz donde los 1 dicen que viene renderizado
    //algo y los 0 no
    let masMesh = mask.scene.children[0];
    masMesh.material.side = DoubleSide;
    masMesh.material.transparent = false;
    masMesh.material.stencilWrite = true;
    masMesh.material.stencilRef = 1; //1 renderiza 0 no
    masMesh.material.stencilFunc = AlwaysStencilFunc; //Todos los 1 renderiz
    masMesh.material.stencilZPass = ReplaceStencilOp;
    //https://learnopengl.com/Advanced-OpenGL/Stencil-testing
  }, [model, mask]);

  return (
    <>
      <primitive object={model.scene} />
      <primitive object={mask.scene} />
      <FillQuad map={target.texture} maskId={1} />
    </>
  );
}
