import { useEffect } from "react";
import { useLoader } from "react-three-fiber";
import { Color, DoubleSide } from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";

export default function Grass() {
  const model = useLoader(
    GLTFLoader,
    process.env.PUBLIC_URL + "/models/grass.glb"
  );

  useEffect(() => {
    if (!model) return;
    const children = model.scene.children[0];
    children.material.alphaToCoverage = true; //Ordena la transparencia del modelo
    children.material.transparent = true;
    children.material.map = children.material.emissiveMap;
    children.material.emissive = new Color(0.5, 0.5, 0.5);
    children.material.side = DoubleSide;
  }, [model]);

  return <primitive object={model.scene} />;
}
