import { PlaneGeometry } from "three";
import { angleToRadians } from "../../Utilitys/angle";
import * as THREE from "three";
import { useEffect, useRef } from "react";
import { useLoader } from "react-three-fiber";
import { TextureLoader } from "three/src/loaders/TextureLoader";
import { math } from "../../Utilitys/math";

// function* enumerate(count) {
//   let i = 0;
//   while (i !== count) yield i++;
// }

class RandomWrapper {
  constructor(_scale, _pers, _lacu, _octa) {
    this.values = {};
    this.params = {
      scale: _scale,
      persistence: _pers,
      lacunarity: _lacu,
      octaves: _octa,
      exponenetation: 2,
      height: 4,
    };
  }

  Rand(x, y) {
    const k = x + "." + y;
    if (!(k in this.values)) {
      this.values[k] = Math.random();
    }
    return this.values[k];
  }

  Noise2D(x, y) {
    const x1 = Math.floor(x);
    const y1 = Math.floor(y);
    // const x2 = x1 + 1;
    // const y2 = y1 + 1;

    const xp = x - x1;
    const yp = y - y1;

    const p11 = this.Rand(x1, y1);
    const p21 = this.Rand(x1, y1);
    const p12 = this.Rand(x1, y1);
    const p22 = this.Rand(x1, y1);

    const px1 = math.lerp(xp, p11, p21);
    const px2 = math.lerp(xp, p12, p22);

    return math.lerp(yp, px1, px2);
  }

  ComputeFBM(x, y) {
    const xs = x / this.params.scale;
    const ys = y / this.params.scale;
    const G = 2.0 ** this.params.persistence;
    let amplitude = 1.0;
    let frequency = 1.0;
    let normalization = 0;
    let total = 0;
    for (let i = 0; i < this.params.octaves; i++) {
      const noiseValue =
        this.Noise2D(xs * frequency, ys * frequency) * 0.5 + 0.5;
      total += noiseValue * amplitude;
      normalization += amplitude;
      amplitude *= G;
      frequency *= this.params.lacunarity;
    }
    total /= normalization;
    return Math.pow(total, this.params.exponenetation) * this.params.height;
  }
}

export default function CustomMesh({ state }) {
  const ref = useRef(null);
  const amplitude = 0.1;
  const planeSize = 10;
  const plane = new PlaneGeometry(
    planeSize,
    planeSize,
    state.resolution,
    state.resolution
  );

  const texture = useLoader(
    TextureLoader,
    process.env.PUBLIC_URL + "/textures/mapa.jpg"
  );

  const random = new RandomWrapper(
    state.scale,
    state.persistence,
    state.lacunarity,
    state.octaves
  );

  useEffect(() => {
    const { geometry } = ref.current;
    const { position } = geometry.attributes;
    for (let index = 0; index < position.count; index++) {
      // 3 + 2 es porque accede solo al valor de z
      // array es de una dimension: [x1, y1, z1, x2,y2... xN, yN, zN]
      const x = index * 3;
      const z = index * 3 + 1;
      const y = index * 3 + 2;
      // let dist = new Vector2(position.array[x], position.array[y]).distanceTo(
      //   new Vector2(0, 0)
      // );
      // dist = Math.pow(dist, 2);
      // position.array[index * 3 + 2] = amplitude * (Math.random() - 0.5);
      position.array[y] = random.ComputeFBM(x, z) * amplitude * state.height;
    }
    position.needsUpdate = true;
    geometry.computeVertexNormals();
  }, [ref, state]);

  const handleClick = (e) => {
    console.log(e);
  };

  return (
    <mesh
      // position={[0, state.height * amplitude, 0]}
      onClick={handleClick}
      ref={ref}
      geometry={plane}
      rotation={[angleToRadians(270), 0, angleToRadians(180)]}
    >
      <meshPhongMaterial
        // map={texture}
        // displacementMap={texture}
        color={state.color}
        side={THREE.DoubleSide}
        wireframe={state.wireFrame}
      />
    </mesh>
  );
}
