import React, { useRef } from "react";
import {
  Environment,
  Float,
  OrbitControls,
  PerspectiveCamera,
} from "@react-three/drei";
import { useFrame } from "react-three-fiber";
import FloatingIsland from "./FloatingIsland";
import Portal from "./Portal";
import Trees from "./Trees";
import FloatingRocks from "./FloatingRocks";
import Grass from "./Grass";
import { GUI } from "dat.gui";

export default function PortalScene() {
  useFrame(({ clock }) => {
    const timeStep = clock.elapsedTime;
    // console.log(timeStep);
  });

  return (
    <>
      {/* CONFIG */}
      <Environment //Only for background
        background={"only"}
        files={process.env.PUBLIC_URL + "/textures/bg.hdr"}
      />
      <Environment //Use to change the light of objects
        background={false}
        files={process.env.PUBLIC_URL + "/textures/envmap.hdr"}
      />

      <PerspectiveCamera
        makeDefault
        fov={50}
        position={[-1.75, 10.85, 20.35]}
      />
      <OrbitControls target={[1, 5, 0]} maxPolarAngle={Math.PI * 0.35} />

      {/* MODELS */}
      <Float speed={0.7} rotationIntensity={0.6} floatIntensity={0.6}>
        <FloatingIsland />
        <Portal />
        <Trees />
        <Grass />
      </Float>

      <FloatingRocks />
    </>
  );
}
