import React from "react";
import styled from "styled-components";

const TopSectionContainer = styled.div`
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: #1756dd32;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 99;
`;

const Logo = styled.h1`
  marigin: 0;
  color: #fff;
  font-weight: 800;
  font-size: 60px;
`;

const Slogan = styled.h4`
  margin: 0;
  color: #fff;
  font-weight: 700;
  font-size: 30px;
`;
const Charity = styled.p`
  font-size: 20px;
  font-weight: 600;
  margin: 10px 0;
  color: white;
  border-top: 2px solid black;
`;
const ButtonContext = styled.button`
  border-radius: 5px;
  font-size: 20px;
  background-color: darkgreen;
  padding: 5px;
  border: 1px solid black;
  cursor: pointer;
  color: white;
`;
export default function WebView() {
  return (
    <>
      <TopSectionContainer>
        <Logo>Global Warming</Logo>
        <Slogan>Keep it cool for safe living</Slogan>
        <Charity>Help us donating 1BTC, for the ones who need it most</Charity>
        <ButtonContext href="#">Donate</ButtonContext>
      </TopSectionContainer>
    </>
  );
}
