import { useEffect, useRef } from "react";
import { EqualStencilFunc } from "three";

const vertexShader = `
  varying vec2 vUv;

  void main(){
    vUv = uv;
    gl_Position = vec4(position.xy, 0.0, 1.0);
  }
`;

const fragmentShader = `
  varying vec2 vUv;

  uniform sampler2D map;

  void main(){
    vec3 col = texture2D(map, vUv).xyz;
    gl_FragColor = vec4(pow(col, vec3(1.75)) * 2.5, 1.0);
  }
`;

const uniforms = {
  map: { type: "t", value: null },
};

const materialProperties = {
  uniforms,
  vertexShader,
  fragmentShader,
  //para no renderizar la profundidad y que otros componentes esten "detras" de esta geo
  depthWrite: false,
  //usa el stencilBuffer
  stencilWrite: true,
  //Funcion que cuando encuentra un valor de ref = maskId renderiza el contenido
  stencilFunc: EqualStencilFunc,
};

export default function FillQuad({ map, maskId }) {
  //El proposito de esto es crear una malla que llene la pantalla
  //y renderizar algo usando el stencilBuffer por lo que solo va a renderizar
  //en las partes donde el buffer tenga asignado 1

  useEffect(() => {
    materialRef.current.uniforms.map.value = map;
  }, [map]);

  const materialRef = useRef();
  return (
    <mesh>
      <planeGeometry args={[2, 2]} />
      <shaderMaterial
        //Permite crear el material pasando los datos
        ref={materialRef}
        attach="material"
        {...materialProperties}
        stencilRef={maskId}
      />
    </mesh>
  );
}
