import { Suspense } from "react";
import { Canvas } from "react-three-fiber";
import BtBack from "../Header/btBack";
import TerrenoScene from "./terrenoScene";
import DatGui from "../Header/datGUI";
import { DatColor, DatNumber, DatFolder, DatBoolean } from "react-dat-gui";
import React from "react";

export default function Index() {
  const [state, setState] = React.useState({
    height: 1,
    color: "blue",
    resolution: 10,
    wireFrame: true,
    scale: 1,
    persistence: 2,
    lacunarity: 1,
    octaves: 1,
  });
  return (
    <>
      <BtBack />
      <DatGui state={state} setState={setState}>
        <DatFolder title="Modelo">
          <DatNumber label="Altura" path="height" min={0} max={2} step={0.1} />
          <DatNumber
            label="Resolucion"
            path="resolution"
            min={8}
            max={250}
            step={5}
          />
          <DatBoolean path="wireFrame" label="Show wireframe" />
          {/* <DatColor path="color" label="Color" /> */}
        </DatFolder>
        <DatFolder title="Ruido">
          <DatNumber label="scale" path="scale" min={1} max={25} step={1} />
          <DatNumber
            label="persistence"
            path="persistence"
            min={1}
            max={25}
            step={1}
          />
          <DatNumber
            label="lacunarity"
            path="lacunarity"
            min={1}
            max={25}
            step={1}
          />
          <DatNumber label="octaves" path="octaves" min={1} max={25} step={1} />
        </DatFolder>
      </DatGui>
      <Canvas className="canvas black">
        <Suspense fallback={null}>
          <TerrenoScene state={state} />
        </Suspense>
      </Canvas>
    </>
  );
}
