import {
  OrbitControls,
  PerspectiveCamera,
  Environment,
} from "@react-three/drei";
import * as THREE from "three";
import { PlaneGeometry } from "three";
import CustomMesh from "./customMesh";
import { angleToRadians } from "../../Utilitys/angle";

export default function TerrenoScene({ state }) {
  return (
    <>
      <PerspectiveCamera makeDefault fov={50} position={[0, 3, 3]} />
      <OrbitControls />

      <ambientLight args={[0xaaffee, 1]} />
      <Environment background>
        <mesh>
          <sphereGeometry args={[50, 100, 100]} />
          <meshBasicMaterial color="#565656" side={THREE.BackSide} />
        </mesh>
      </Environment>
      <CustomMesh state={state} />
      {/* <mesh rotation={[angleToRadians(90), 0, 0]}>
        <axesHelper />
        <planeGeometry args={[10, 10, 20, 20]} />
        <meshBasicMaterial color={"white"} side={THREE.DoubleSide} wireframe />
      </mesh> */}
    </>
  );
}
